import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			StatusEnumMap: misEnum.FoodRecordEnumMap,
			steps: [],
			color: '',
			info: {},
			id: ""
		}
	},
	onLoad(option) {
		this.id = option.id;
	},
	onShow() {
		this.steps.length = 0;
		global.$http.post('/dining/record/recordInfo', {
			params: {
				record_id: this.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
				//设置进度步骤
				if (this.info.status === 0) {
					this.color = 'text-cyan';
				} else if (this.info.status === -2) {
					this.color = 'text-orange';
				} else if (this.info.status === -1) {
					this.color = 'text-red';
				} else if (this.info.status === 1) {
					this.color = 'text-green';
				}
				var isFind = false;
				misEnum.FoodRecordEnumMap.forEach((value, key, map) => {
					var cuIcon = '';
					var color = '';
					if (!isFind || key == this.info.status) {
						color = this.color;
					}
					if (key == this.info.status) {
						isFind = true;
					}
					if (key === 0) {
						cuIcon = 'usefullfill';
					} else if (key === -2) {
						cuIcon = 'radioboxfill';
					} else if (key === -1) {
						cuIcon = 'roundclosefill';
					} else if (key === 1) {
						cuIcon = 'roundcheckfill';
					}
					this.steps.push({
						cuIcon: cuIcon,
						name: value,
						key: key,
						color: color
					})
				});

			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		toEdit: function(e) {
			uni.navigateTo({
				url: '../food/selectTime?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		},
		cancleBook: function() {
			uni.showModal({
				title: '提示',
				content: '确定取消预定？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.cancel) return;
					uni.showLoading({
						title: '提交中',
						mask: false
					});
					global.$http.post('/dining/record/cancelMyRecord', {
						params: {
							record_id: this.info.id
						},
					}).then(res => {
						uni.hideLoading();
						if (res.status === "0") {
							uni.showToast({
								title: "取消成功",
								icon: 'none'
							});
							this.info.status = -2;
							this.steps[0].color = 'text-orange';
							this.steps[1].color = 'text-orange';
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.hideLoading();
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
